import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { ExtractionMassified } from 'src/app/models/extraction-massified.model';
import { ResponseList } from 'src/app/models/response-list.interface';
import { MediaUpload } from 'src/app/models/media-upload.interface';

@Injectable({providedIn: 'root'})
export class ImportationService {
    
    constructor(
        private httpClient: HttpClient,
    ) { }

    public getFile(link: string): Observable<any> {
        return this.httpClient.get<any>(link, {
            responseType : 'blob' as 'json',
            observe : 'response'
        });
    }    

    public getTypes(): Observable<ResponseList> {
        return this.httpClient.get<ResponseList>(`${environment.urlServices}/api/v1/central-uploads/tipos`, {})
    }    
        
    public findUpload(pageIndex: string, 
                        pageSize: string, 
                        nomeArquivo?: string,
                        statusId?: string,
                        tipoId?: string,
                        dataSolicitacaoInicial?: string,
                        dataSolicitacaoFinal?: string
                         ): Observable<ResponseList> {
        return this.httpClient.get<ResponseList>(`${environment.urlServices}/api/v1/central-uploads`, {
            params : {
                PageSize: pageSize,
                PageIndex: pageIndex,
                NomeArquivo: nomeArquivo,
                StatusId: statusId,
                TipoId: tipoId,
                DataSolicitacaoInicial: dataSolicitacaoInicial,
                DataSolicitacaoFinal: dataSolicitacaoFinal
            }
        })
    }

    public sendUpload(file: any): Observable<any> {
        return this.httpClient.post<Observable<any>>(`${environment.urlServices}/api/v1/central-uploads`, file)
    }
			

}