import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CentralUploadComponent } from './central-upload/central-upload.component';
import { ImportationRoutesGuard } from './importation-routes.guard';


const routes: Routes = [
	{ path: 'central-upload', component: CentralUploadComponent, canActivate: [ImportationRoutesGuard] },
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule]
})
export class ImportationRoutingModule { }
