import { Component, OnInit, ElementRef, ViewChild } from '@angular/core';
import { ImportationService } from '../services/importation.service';
import { SnackbarComponent } from 'src/app/shared/components/snackbar/snackbar.component';
import { MatSnackBar, MatTableDataSource, MatPaginator, PageEvent } from '@angular/material';
import { MediaUpload } from 'src/app/models/media-upload.interface';
import { Atividade } from 'src/app/models/atividade.interface';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ICentralUpload, FileCentralDownload, ITipoUpload } from 'src/app/models/central-upload.interface';

@Component({
	selector: 'central-upload',
	templateUrl: './central-upload.component.html',
	styleUrls: ['./central-upload.component.scss']
})

export class CentralUploadComponent implements OnInit {

	@ViewChild('myInput', { static: false }) myInput: ElementRef;
	@ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
	public filterType: string = '';
	public filterFileName: string = '';
	public tipos: ITipoUpload[] = [];
	public file: Set<File> = new Set()
	public loading: boolean = false;
	public isUploadFiles: boolean = false;
	public anexos: MediaUpload[] = [];
	public dataSource = new MatTableDataSource<ICentralUpload>([]);
	public loader: Boolean;
	public formData: FormData = new FormData();
	public form: FormGroup;
	public length: number;
	public pageSize = 5;
	public pageIndex = 0;
	public pageSizeOptions: number[] = [5, 10, 25, 50, 100];
	public displayedColumns: string[] = ['CriadoPor', 'DataCadastro', 'Tipo', 'Arquivo', 'Status', 'Mensagem', 'Actions'];

	constructor(
		private importationService: ImportationService,
		private matSnackBar: MatSnackBar,
		private formBuilder: FormBuilder
	) { }

	ngOnInit() {
		this.form = this.formBuilder.group({
			tipoUpload: ['', [Validators.required]]
		});
		this.getTypes();
		this.find();
	}

	public submit() {
		try {
			this.loading = true;
			let fileToUpload = this.formData.get("file");
			const formData: FormData = new FormData();
			formData.append('file', fileToUpload);
			formData.append("tipoUpload", this.form.value.tipoUpload.toString());
			this.importationService.sendUpload(formData).subscribe(
				() => {
					this.loading = false;
					SnackbarComponent.showSuccess(this.matSnackBar, "Upload iniciado, acompanhe seu progresso na tabela abaixo.");
					this.find();
					this.myInput.nativeElement.value = "";
					this.isUploadFiles = false;

				},
				() => {
					this.loading = false;
					SnackbarComponent.showError(this.matSnackBar, null, "Erro no upload do arquivo");
					this.myInput.nativeElement.value = "";
					this.isUploadFiles = false;
				}
			);
		}
		catch
		{
			this.loading = false;
		}
	}

	public getTypes(): void {
		this.importationService.getTypes().subscribe(
			response => {
				this.tipos = [...response.data];
			},
			() => {
				// Mensagem de Erro para Servi√ßo Indispon√≠vel. N√£o tem como prosseguir
			}
		);
	}	

	public find(reset: boolean = false): void {
		this.loading = true;

		if (reset){
			this.pageIndex = 0;
			this.pageSize = 5;
		}

		const pageIndex = (this.pageIndex + 1).toString();
		const pageSize = this.pageSize.toString();
		this.importationService.findUpload(pageIndex, pageSize, this.filterFileName, '', this.filterType, '', '').subscribe(
			response => {
				this.loading = false;
				this.dataSource = new MatTableDataSource<ICentralUpload>(response.data);
				this.length = response.length;
			},
			error => {
				SnackbarComponent.showError(this.matSnackBar, error);
				this.loading = false;
			}
		);
	}

	public getDownload(file: FileCentralDownload): void {
		this.importationService.getFile(file.url).subscribe(
			response => {
				const blob = new Blob([response.body], { type: 'application/octet-stream' });
				const linkArquivo = window.URL.createObjectURL(blob);
				const fileName = response.headers.get('content-disposition').split(';')[1].split('filename')[1].split('=')[1].trim();
				var link = document.createElement("a");
				link.setAttribute("href", linkArquivo.toString());
				link.setAttribute("download", fileName);
				link.style.display = "none";
				document.body.appendChild(link);
				link.click();
				document.body.removeChild(link);
			}
		)
	}

	public setPageSizeOptions(eventOptions: PageEvent) {
		this.pageIndex = eventOptions.pageIndex;
		this.pageSize = eventOptions.pageSize;
		this.find();
	}

	public onFileChanged(files: FileList) {
		this.formData = new FormData();
		if (files && files.length > 0) {
			try {
				for (let i = 0; i < files.length; i++) {
					const fileToUpload: File = files.item(i);
					this.formData.append('file', fileToUpload, fileToUpload.name);
				}
				this.isUploadFiles = true;
			}
			catch
			{
				this.isUploadFiles = false;
				this.formData = new FormData();
			}
		}
	}



}