import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ImportationRoutingModule } from './importation-routing.module';
import { SharedModule } from 'src/app/shared/shared.module';
import { MatFormFieldModule, MatInputModule, MatSelectModule, MatDatepickerModule, MatNativeDateModule, DateAdapter, MAT_DATE_FORMATS, MatRadioModule, MatProgressSpinnerModule, MatProgressBarModule, MatTableModule, MatPaginatorModule, MatCheckboxModule, MatDividerModule, MatCardModule, MatMenuModule } from '@angular/material';
import { ReactiveFormsModule } from '@angular/forms';
import { NgxMaskModule } from 'ngx-mask';
import { CustomDateAdapter } from 'src/app/shared/dateAdapter/customDate.adapter';
import { MY_DATE_FORMATS } from 'src/app/shared/dateAdapter/my-date-formats.const';
import { CentralUploadComponent } from './central-upload/central-upload.component';

@NgModule({
	declarations: [
		CentralUploadComponent
	],
	imports: [
		CommonModule,
		SharedModule,
		MatMenuModule,
		MatCardModule,
		MatCheckboxModule,
		MatDatepickerModule,
		MatNativeDateModule,
		MatDividerModule,
		MatFormFieldModule,
		MatInputModule,
		MatProgressSpinnerModule,
		MatProgressBarModule,
		MatRadioModule,
		MatSelectModule,
		MatPaginatorModule,
		MatTableModule,
		MatProgressSpinnerModule,
		MatProgressBarModule,
		NgxMaskModule.forRoot(),
		ReactiveFormsModule,
		ImportationRoutingModule
	],
	providers: [
		{ provide: DateAdapter, useClass: CustomDateAdapter },
		{ provide: MAT_DATE_FORMATS, useValue: MY_DATE_FORMATS }
	],
	entryComponents: [
	]
})
export class ImportationModule { }
